import React from "react";
import ProgressBar from "react-bootstrap/ProgressBar";
export default function Progressbar({label, color, value}) {
  return (
    <div>
      <div className='pt-3'>
        <label htmlFor=''>{label}</label>
        <ProgressBar animated variant={`${color}`} now={value} label={`${value}%`} />
      </div>
    </div>
  );
}
