import React from "react";

export default function Iconsbox({icon, title, text}) {
  return (
    <div className='col-md-6  col-lg-4 my-3 text-center text-lg-left'>
      <div className='iconbox row align-items-center'>
        <div className='col-md-4  '>
          <div className='icon '>
            <i class={`fa  ${icon}`} aria-hidden='true'></i>
          </div>
        </div>
        <div className='col-md-8 '>
          <h4>{title}</h4>
          <p classname='text-secondary'>{text}</p>
        </div>
      </div>
    </div>
  );
}
