import React from "react";

export default function Recentworkbox({workimg, modelid}) {
  return (
    <div className='my-3 my-lg-0'>
      <div className='workbox'>
        <img className='img-fluid' src={workimg} alt='' />
        <div className='workbox-content  w-100 h-100'>
          <h4 className='font-weight-bold'>Business Theme</h4>
          <p>There are many variations of passages of Lorem Ipsum available, but the majority</p>
          <a type='button' className='fa fa-eye' data-toggle='modal' data-target={`#${modelid}`}>
            View
          </a>
          <div className='modal  fade  ' id={`${modelid}`} tabindex='-1' role='dialog' aria-labelledby='modelTitleId' aria-hidden='true'>
            <div className='modal-dialog  modal-dialog-centered' role='document'>
              <div className='modal-content '>
                <img className='img-thumbnail' src={workimg} alt='' />
                <div className='modal-footer py-2'>
                  <i data-dismiss='modal' class='fa fa-times text-dark fa-1x' aria-hidden='true'></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
