import React, {Component} from "react";
import Heading from "../Common/Heading";
import Blogsingle from "../Common/Blogsingle";
import Comments from "../Common/Comments";
import blogimg1 from "../../images/blog/blog1.jpg";
import blogimg2 from "../../images/blog/blog2.jpg";
import Archive from "../Common/Archive";
import Category from "../Common/Category";
import Tagcloud from "../Common/Tagcloud";
import Ourgallery from "../Common/Ourgallery";
import Footer from "../Footer/Footer";

export default class Blog extends Component {
  render() {
    return (
      <div className='blog pt-5 p-lg-0'>
        <Heading
          heading='Blogs'
          para='Pellentesque habitant morbi tristique senectus et netus et malesuada'
        />

        <div className='container'>
          <div className='row justify-content-center'>
            <div className='col-lg-8'>
              <div className='row'>
                <div className='col-lg-3 blog-left mt-4'>
                  <div className='btn btn-danger col-lg-9'>07 Nov </div>
                  <button className='btn btn-sm col-lg-9 p-0 m-0 '>
                    <i class='fa fa-user' aria-hidden='true'></i> John Doe
                  </button>
                  <button className='btn btn-sm col-lg-9 p-0 m-0 '>
                    <i class='fa fa-envelope-o' aria-hidden='true'></i> 2 Comments
                  </button>
                  <button className='btn btn-sm  col-lg-9 p-0 m-0 '>
                    <i class='fa fa-thumbs-o-up' aria-hidden='true'></i>56 Likes
                  </button>
                </div>
                <div className='col-lg-9'>
                  <Blogsingle img={blogimg1} />
                </div>
              </div>
              <div className='row'>
                <div className='col-lg-3 blog-left mt-4'>
                  <div className='btn btn-danger col-lg-9'>07 Nov </div>
                  <button className='btn btn-sm col-lg-9 p-0 m-0 '>
                    <i class='fa fa-user' aria-hidden='true'></i> John Doe
                  </button>
                  <button className='btn btn-sm col-lg-9 p-0 m-0 '>
                    <i class='fa fa-envelope-o' aria-hidden='true'></i> 2 Comments
                  </button>
                  <button className='btn btn-sm  col-lg-9 p-0 m-0 '>
                    <i class='fa fa-thumbs-o-up' aria-hidden='true'></i>56 Likes
                  </button>
                </div>
                <div className='col-lg-9'>
                  <Blogsingle img={blogimg2} />
                </div>
              </div>
            </div>
            <div className='col-lg-4'>
              <input
                type='search'
                className='form-control search_box my-4 '
                placeholder='Search Here'
                name=''
                id=''
              />

              <p>RECENT COMMENTS</p>
              <Comments />
              <Comments />
              <Comments />

              <Category />
              <Archive />
              <Tagcloud />
              <Ourgallery />
            </div>
          </div>
        </div>
        <input
          className='col-lg-6'
          type='text'
          value='whis is real? How do You define real'
          name=''
          id=''
          onSave={this.onsave}
        />
        {/* footer */}
        <div className='footer'>
          <div className='container py-5'>
            <Footer />
          </div>
        </div>
      </div>
    );
  }
}
