import React, {Component} from "react";

export default class RoundSkill extends Component {
  render() {
    const {value, content, bgcolor} = this.props;
    const style = {
      height: `${value}%`,
      backgroundColor: `${bgcolor}`,
    };

    return (
      <div className='col-lg-3'>
        <div className='roundskill mx-auto my-3'>
          <div className='round-color' style={style}></div>
          <div className='round-content'>
            <h2 className='font-weight-bold'>{value}%</h2>
            <h4 className='font-weight-bold'>{content}</h4>
          </div>
        </div>
      </div>
    );
  }
}
