import React, {Component} from "react";
import {Link} from "react-router-dom";
import logo from "../../images/logo.png";

export default class Navbar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeEventKey: "0",
    };
  }

  activeColor = "#dc3545";
  activetext = "#fff";

  render() {
    return (
      <div>
        <nav className='navbar navbar-expand-md navbar-dark px-lg-5 bg-dark fixed-top'>
          <a className='navbar-brand' href='#'>
            <img src={logo} alt='' />
          </a>
          <button
            className='navbar-toggler d-lg-none'
            type='button'
            data-toggle='collapse'
            data-target='#collapsibleNavId'
            aria-controls='collapsibleNavId'
            aria-expanded='false'
            aria-label='Toggle navigation'>
            <span className='navbar-toggler-icon'></span>
          </button>
          <div className='collapse navbar-collapse' id='collapsibleNavId'>
            <ul className='navbar-nav ml-auto mt-2 mt-lg-0'>
              <li className='nav-item mx-lg-3'>
                <Link
                  to={"/Reactproject1"}
                  className='nav-link text-white px-2 py-1 btn btn-sm text-left '
                  eventKey='0'
                  onClick={() => this.setState({activeEventKey: "0"})}
                  {...(this.state.activeEventKey === "0" && {
                    style: {backgroundColor: this.activeColor, color: this.activetext},
                  })}>
                  Home
                </Link>
              </li>
              <li className='nav-item mx-lg-3'>
                <Link
                  to={"/about"}
                  className='nav-link text-white px-2 py-1 btn btn-sm text-left'
                  href='#'
                  eventKey='1'
                  onClick={() => this.setState({activeEventKey: "1"})}
                  {...(this.state.activeEventKey === "1" && {
                    style: {backgroundColor: this.activeColor, color: this.activetext},
                  })}>
                  About Us
                </Link>
              </li>
              <li className='nav-item mx-lg-3'>
                <Link
                  to={"/services"}
                  className='nav-link text-white px-2 py-1 btn btn-sm text-left'
                  href='#'
                  eventKey='2'
                  onClick={() => this.setState({activeEventKey: "2"})}
                  {...(this.state.activeEventKey === "2" && {
                    style: {backgroundColor: this.activeColor, color: this.activetext},
                  })}>
                  Services
                </Link>
              </li>
              <li className='nav-item mx-lg-3'>
                <Link
                  to={"/Portfolio"}
                  className='nav-link text-white px-2 py-1 btn btn-sm text-left'
                  href='#'
                  eventKey='3'
                  onClick={() => this.setState({activeEventKey: "3"})}
                  {...(this.state.activeEventKey === "3" && {
                    style: {backgroundColor: this.activeColor, color: this.activetext},
                  })}>
                  Portfolio
                </Link>
              </li>

              <li className='nav-item mx-lg-3'>
                <Link
                  to='./blog'
                  className='nav-link text-white px-2 py-1 btn btn-sm text-left'
                  href='#'
                  eventKey='5'
                  onClick={() => this.setState({activeEventKey: "5"})}
                  {...(this.state.activeEventKey === "5" && {
                    style: {backgroundColor: this.activeColor, color: this.activetext},
                  })}>
                  Blog
                </Link>
              </li>
              <li className='nav-item mx-lg-3'>
                <Link
                  to='./contact'
                  className='nav-link text-white px-2 py-1 btn btn-sm text-left'
                  href='#'
                  eventKey='6'
                  onClick={() => this.setState({activeEventKey: "6"})}
                  {...(this.state.activeEventKey === "6" && {
                    style: {backgroundColor: this.activeColor, color: this.activetext},
                  })}>
                  Contact
                </Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}
