import React, {Component} from "react";
import Heading from "../Common/Heading";
import Slider from "../Common/Slider";
import banner1 from "../../images/slider_one.jpg";
import RoundSkill from "../Common/RoundSkill";
import SingleTimeline from "../Common/SingleTimeline";
import Footer from "../Footer/Footer";
import timeline1 from "../../images/man1.jpg";
import timeline2 from "../../images/man2.jpg";
import timeline3 from "../../images/man3.jpg";
import timeline4 from "../../images/man4.jpg";

export default class About extends Component {
  render() {
    return (
      <div>
        <div className='aboutus my-5'>
          {/* heading */}
          <Heading
            heading='About Corlate'
            para='Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
             et dolore magna aliqua. Ut enim ad minim veniam'
          />
          <div className='container '>
            <Slider bannerimg1={banner1} bannerimg2={banner1} bannerimg3={banner1} />
          </div>
        </div>
        <Heading
          heading='Our Skill'
          para='Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
             et dolore magna aliqua. Ut enim ad minim veniam'
        />
        <div className='container'>
          <div className='row justify-content-between '>
            <RoundSkill value='85' content='JOOMLA' bgcolor='#2d7da4' />
            <RoundSkill value='70' content='HTML' bgcolor='#6aa42f' />
            <RoundSkill value='90' content='CSS' bgcolor='#ffbd20' />
            <RoundSkill value='55' content='WORD PRESS' bgcolor='#db3615' />
          </div>
        </div>
        {/* team */}
        <Heading
          heading='Team of Corlate'
          para='Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
             et dolore magna aliqua. Ut enim ad minim veniam'
        />
        <div className='container'>
          <div className=' row '>
            <div className='col-lg-4 timeline1'>
              <SingleTimeline timelineimg={timeline1} />
            </div>
            <div className='col-lg-4  timeline2'>
              <SingleTimeline timelineimg={timeline2} />
            </div>
          </div>
          <div className='d-none d-lg-block'>
            <div className='row time-code align-items-center'>
              <hr className='bg-primary' width='50px' />
              <i class='fa fa-angle-up bg-primary' aria-hidden='true'></i>
              <hr className=' col-lg bg-success' />
              <i class='fa fa-angle-down bg-success' aria-hidden='true'></i>
              <hr className=' col-lg bg-warning' />
              <i class='fa fa-angle-up bg-warning' aria-hidden='true'></i>
              <hr className='col-lg bg-danger' />
              <i class='fa fa-angle-down bg-danger' aria-hidden='true'></i>
              <hr className='col-lg bg-dark' />
            </div>
          </div>
          <div className=' row  justify-content-lg-end'>
            <div className='col-lg-4 timeline3'>
              <SingleTimeline timelineimg={timeline3} />
            </div>
            <div className='col-lg-4  timeline4'>
              <SingleTimeline timelineimg={timeline4} />
            </div>
          </div>
        </div>
        {/* footer */}
        <div className='footer'>
          <div className='container py-5'>
            <Footer />
          </div>
        </div>
      </div>
    );
  }
}
