import React from "react";
import avatarimg from "../../images/blog/avatar3.png";
export default function Comments() {
  return (
    <div className='row justify-content-center align-items-center text-center text-lg-left mt-3'>
      <img className='col-lg-3 col-5' src={avatarimg} alt='' />
      <div className='col-lg-9'>
        <p className='m-0'>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do</p>
        <small>
          By <span className='text-danger'>Alex</span> On
          <span className='text-danger'> Creative</span>
        </small>
      </div>
    </div>
  );
}
