import React, {Component} from "react";
import workimg1 from "../../images/portfolio/recent/item1.png";
import workimg2 from "../../images/portfolio/recent/item2.png";
import workimg3 from "../../images/portfolio/recent/item3.png";
import workimg4 from "../../images/portfolio/recent/item4.png";
import workimg5 from "../../images/portfolio/recent/item5.png";
import workimg6 from "../../images/portfolio/recent/item6.png";
import workimg7 from "../../images/portfolio/recent/item7.png";
import workimg8 from "../../images/portfolio/recent/item8.png";
import Recentworkbox from "../Common/Recentworkbox";
import Portfolio from "../Portfolio/Portfolio";

import {BrowserRouter as Router, Switch, Route, NavLink} from "react-router-dom";

export class Portfoliobox extends Component {
  render() {
    return (
      <div>
        <div className='container-fluid'>
          <Router>
            <div className='d-flex flex-wrap flex-column  flex-md-row justify-content-md-center'>
              <NavLink
                to='/portfolio'
                className='btn btn-sm border m-3 main-nav'
                activeClassName='main-nav-active'>
                ALL WORK
              </NavLink>

              <NavLink
                to='/creative'
                className='btn btn-sm border m-3 main-nav'
                activeClassName='main-nav-active'>
                CREATIVE
              </NavLink>
              <NavLink
                to='/photography'
                className='btn btn-sm border m-3  main-nav'
                activeClassName='main-nav-active'>
                PHOTOGRAPHY
              </NavLink>
              <NavLink
                to='/webdevelopment'
                className='btn btn-sm border m-3  main-nav'
                activeClassName='main-nav-active'>
                WEB DEVELOPMENT
              </NavLink>
            </div>
            <Switch>
              <Route path='/portfolio' component={Portfolio}>
                <div className='row justify-content-center my-5'>
                  <Recentworkbox workimg={workimg1} modelid='modelid1' />
                  <Recentworkbox workimg={workimg2} modelid='modelid2' />
                  <Recentworkbox workimg={workimg3} modelid='modelid3' />
                  <Recentworkbox workimg={workimg4} modelid='modelid4' />
                  <Recentworkbox workimg={workimg5} modelid='modelid5' />
                  <Recentworkbox workimg={workimg6} modelid='modelid6' />
                  <Recentworkbox workimg={workimg7} modelid='modelid7' />
                  <Recentworkbox workimg={workimg8} modelid='modelid8' />
                </div>
              </Route>
            </Switch>

            <Route path='/creative'>
              <div className='row justify-content-center my-5'>
                <Recentworkbox workimg={workimg1} modelid='modelid1' />
                <Recentworkbox workimg={workimg2} modelid='modelid2' />
                <Recentworkbox workimg={workimg3} modelid='modelid3' />
                <Recentworkbox workimg={workimg4} modelid='modelid4' />
              </div>
            </Route>
            <Route path='/photography'>
              <div className='row justify-content-center my-5'>
                <Recentworkbox workimg={workimg5} modelid='modelid5' />
                <Recentworkbox workimg={workimg6} modelid='modelid6' />
                <Recentworkbox workimg={workimg7} modelid='modelid7' />
                <Recentworkbox workimg={workimg8} modelid='modelid8' />
              </div>
            </Route>
            <Route path='/webdevelopment'>
              <div className='row justify-content-center my-5'>
                <Recentworkbox workimg={workimg2} modelid='modelid2' />
                <Recentworkbox workimg={workimg3} modelid='modelid3' />
                <Recentworkbox workimg={workimg6} modelid='modelid6' />
                <Recentworkbox workimg={workimg4} modelid='modelid4' />
                <Recentworkbox workimg={workimg7} modelid='modelid7' />
                <Recentworkbox workimg={workimg8} modelid='modelid8' />
              </div>
            </Route>
          </Router>
        </div>
      </div>
    );
  }
}

export default Portfoliobox;
