import React, {Component} from "react";
import {Accordion, Card} from "react-bootstrap";
export default class Accordionmenu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeEventKey: "0",
    };
  }

  activeColor = "#000";
  activetext = "#fff";

  render() {
    const {accimg} = this.props;
    return (
      <div>
        <Accordion defaultActiveKey='0'>
          <Card event>
            <Card.Header
              {...(this.state.activeEventKey === "0" && {
                style: {backgroundColor: this.activeColor, color: this.activetext},
              })}>
              <Accordion.Toggle
                as={Card.Header}
                variant='link'
                eventKey='0'
                onClick={() => this.setState({activeEventKey: "0"})}>
                <div className='row justify-content-between align-items-center p-0 m-0'>
                  <span className='pl-3'>Click me Accordion1 !!!</span>
                  <button className='btn btn-danger font-weight-bold '>
                    <i class='fa fa-angle-right ' aria-hidden='true'></i>
                  </button>
                </div>
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey='0'>
              <Card.Body>
                <div className='row align-items-center text-center text-lg-left '>
                  <div className='col-md-4'>
                    <img classname='' src={accimg} alt='' />
                  </div>
                  <div className='col-md-8'>
                    <h5>Adipisicing elit</h5>
                    <p className='text-justify'>
                      Hello! I'm the body Lorem ipsum dolor sit amet consectetur adipisicing elit.
                      Soluta enim dolor tempora illum inventore qui sit.
                    </p>
                  </div>
                </div>
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <Card.Header
              {...(this.state.activeEventKey === "1" && {
                style: {backgroundColor: this.activeColor, color: this.activetext},
              })}>
              <Accordion.Toggle
                as={Card.Header}
                variant='link'
                eventKey='1'
                onClick={() => this.setState({activeEventKey: "1"})}>
                <div className='row justify-content-between align-items-center p-0 m-0'>
                  <span className='pl-3'>Click me Accordion2 !!!</span>
                  <button className='btn btn-danger font-weight-bold '>
                    <i class='fa fa-angle-right ' aria-hidden='true'></i>
                  </button>
                </div>
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey='1'>
              <Card.Body>
                Hello! I'm another body Lorem ipsum dolor sit amet consectetur adipisicing elit.
                Suscipit ipsa vel odio cumque enim! Corrupti expedita eos impedit aut accusamus.
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <Card.Header
              {...(this.state.activeEventKey === "2" && {
                style: {backgroundColor: this.activeColor, color: this.activetext},
              })}>
              <Accordion.Toggle
                as={Card.Header}
                variant='link'
                eventKey='2'
                onClick={() => this.setState({activeEventKey: "2"})}>
                <div className='row justify-content-between align-items-center p-0 m-0'>
                  <span className='pl-3'>Click me Accordion3 !!!</span>
                  <button className='btn btn-danger font-weight-bold '>
                    <i class='fa fa-angle-right ' aria-hidden='true'></i>
                  </button>
                </div>
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey='2'>
              <Card.Body>
                Hello! I'm another body Lorem ipsum dolor sit amet consectetur adipisicing elit.
                Nemo laboriosam dolorem consectetur quo ipsa sunt, sint hic. Debitis, nobis neque?
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <Card.Header
              {...(this.state.activeEventKey === "3" && {
                style: {backgroundColor: this.activeColor, color: this.activetext},
              })}>
              <Accordion.Toggle
                as={Card.Header}
                variant='link'
                eventKey='3'
                onClick={() => this.setState({activeEventKey: "3"})}>
                <div className='row justify-content-between align-items-center p-0 m-0'>
                  <span className='pl-3'>Click me Accordion4 !!!</span>
                  <button className='btn btn-danger font-weight-bold '>
                    <i class='fa fa-angle-right ' aria-hidden='true'></i>
                  </button>
                </div>
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey='3'>
              <Card.Body>
                Hello! I'm another body Lorem ipsum dolor sit amet consectetur adipisicing elit.
                Suscipit ipsa vel odio cumque enim! Corrupti expedita eos impedit aut accusamus.
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>
      </div>
    );
  }
}
