import React, {Component} from "react";
import Heading from "./Heading";
import partner1 from "../../images/partners/partner1.png";
import partner2 from "../../images/partners/partner2.png";
import partner3 from "../../images/partners/partner3.png";
import partner4 from "../../images/partners/partner4.png";
import partner5 from "../../images/partners/partner5.png";
export default class Ourpartners extends Component {
  render() {
    return (
      <div className='ourpartners py-5  '>
        <Heading
          heading='Our Partners'
          headingcolor='text-white'
          paracolor='text-white'
          para='Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
et dolore magna aliqua. Ut enim ad minim veniam'
        />
        <div className='container'>
          <div className='row  justify-content-around'>
            <img className='img-fluid my-3 my-lg-0' width='100px' src={partner1} alt='' />
            <img className='img-fluid my-3 my-lg-0' width='100px' src={partner2} alt='' />
            <img className='img-fluid my-3 my-lg-0' width='100px' src={partner3} alt='' />
            <img className='img-fluid my-3 my-lg-0' width='100px' src={partner4} alt='' />
            <img className='img-fluid my-3 my-lg-0' width='100px' src={partner5} alt='' />
          </div>
        </div>
      </div>
    );
  }
}
