import React, {Component} from "react";
import Carousel from "react-bootstrap/Carousel";

export default class slider extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    const {bannerimg1, bannerimg2, bannerimg3} = this.props;
    const {heading1, heading2, heading3} = this.props;
    const {paragraph1, paragraph2, paragraph3} = this.props;
    const {btn1color, btn2color, btn3color} = this.props;
    const {btn1, btn2, btn3} = this.props;
    const {textalign} = this.props;
    const {contentimg1, contentimg2, contentimg3} = this.props;

    return (
      <div>
        <Carousel>
          {/* SLIDE1 */}
          <Carousel.Item className='slider'>
            <img className='img-fluid' src={bannerimg1} alt='First slide' />
            <img className='content-img d-none d-lg-block' src={contentimg1} alt='' />
            <Carousel.Caption className={`slide-content ${textalign} d-none d-lg-block`}>
              <h3 className='col-lg-5'>{heading1}</h3>
              <p className='col-lg-5'>{paragraph1}</p>
              <button className={`btn ml-lg-3 btn-${btn1color}`}>{btn1}</button>
            </Carousel.Caption>
          </Carousel.Item>

          {/* SLIDE2 */}
          <Carousel.Item className='slider'>
            <img className='img-fluid' src={bannerimg2} alt='Second slide' />
            <img className='content-img d-none d-lg-block' src={contentimg2} alt='' />

            <Carousel.Caption className={`slide-content ${textalign} d-none d-lg-block`}>
              <h3 className='col-lg-5'>{heading2}</h3>
              <p className='col-lg-5'>{paragraph2}</p>
              <button className={`btn ml-lg-3 btn-${btn2color}`}>{btn2}</button>
            </Carousel.Caption>
          </Carousel.Item>

          {/* SLIDE3 */}
          <Carousel.Item className='slider'>
            <img className='img-fluid' src={bannerimg3} alt='Third slide' />
            <img className='content-img d-none d-lg-block' src={contentimg3} alt='' />

            <Carousel.Caption className={`slide-content ${textalign} d-none d-lg-block`}>
              <h3 className='col-lg-5'>{heading3}</h3>
              <p className='col-lg-5'>{paragraph3}</p>
              <button className={`btn ml-lg-3 btn-${btn3color}`}>{btn3}</button>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      </div>
    );
  }
}
