import React, {Component} from "react";

export default class SingleTimeline extends Component {
  render() {
    const {timelineimg} = this.props;
    return (
      <div>
        <div className='p-3 my-4 border'>
          <div className='row justify-content-around'>
            <div className='m-2'>
              <img className='rounded-circle' src={timelineimg} alt='' />
            </div>
            <div className='m-2'>
              <h5 className='m-0'>JHON DOE</h5>
              <p className='m-0'>Founder and CEO</p>
              <button className='btn btn-outline-danger btn-sm py-0 px-1'>WEB</button>
              <button className='btn btn-outline-danger btn-sm py-0 px-1 m-1'>UI</button>
              <button className='btn btn-outline-danger btn-sm py-0 px-1 m-1'>UX</button>
              <br />
              <button className='btn btn-outline-danger btn-sm py-0 px-1 '>PHOTOSHOP</button>
              <br />
              <i class='fa fa-facebook  my-2' aria-hidden='true'></i>
              <i class='fa fa-twitter my-2 ml-2' aria-hidden='true'></i>
              <i class='fa fa-google-plus my-2 ml-2' aria-hidden='true'></i>
            </div>
          </div>
          <p className='text-justify'>
            There are many variations of passages of Lorem Ipsum available, but the majority have
            suffered alteration in some form, by injected humour, or randomised words which don't
            look even slightly believable.
          </p>
        </div>
      </div>
    );
  }
}
