import React from "react";
import Heading from "../Common/Heading";
import Map from "../Common/Map";
import ContactLinks from "../Common/ContactLinks";
import Footer from "../Footer/Footer";
import ContactForm from "../Common/ContactForm";

function Contact() {
  return (
    <div className='contact mt-5 m-lg-0'>
      <Heading
        heading='How to Reach Us?'
        para='Lorem ipsum dolor sit amet, consectetur adipisicing elit'
      />
      <div className='contactbg row justify-content-center align-items-center m-auto text-center text-lg-left'>
        <div className='col-lg-3'>
          <Map />
        </div>
        <div className='col-lg-6'>
          <div className='row justify-content-center'>
            <div className='col-lg-5 my-2'>
              <ContactLinks office='Head Office' />
            </div>
            <div className='col-lg-5 my-2'>
              <ContactLinks office='Zonal #2 Office' />
            </div>
            <div className='col-lg-5 my-2'>
              <ContactLinks office='Zonal Office' />
            </div>
            <div className='col-lg-5 my-2'>
              <ContactLinks office='Zonal #3 Office' />
            </div>
          </div>
        </div>
      </div>
      <Heading
        heading='Drop Your Message'
        para='Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.'
      />
      <ContactForm />
      {/* footer */}
      <div className='footer'>
        <div className='container py-5'>
          <Footer />
        </div>
      </div>
    </div>
  );
}

export default Contact;
