import React from "react";
import bottomimg from "../../images/image_bg.png";

export default function Client({clientimg}) {
  return (
    <div className='client col-lg-4 my-4 text-center'>
      <img className='client-img' src={clientimg} alt='' />
      <br />
      <img className='client-bottom-img' src={bottomimg} width='40px' alt='' />
      <div className='client-content'>
        <p className=' mb-1'>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt
        </p>
        <p>
          <strong>-John Doe / </strong>Director of corlate.com
        </p>
      </div>
    </div>
  );
}
