import React from "react";
import gallery1 from "../../images/blog/gallery1.png";
import gallery2 from "../../images/blog/gallery2.png";
import gallery3 from "../../images/blog/gallery3.png";
import gallery4 from "../../images/blog/gallery4.png";
import gallery5 from "../../images/blog/gallery5.png";
import gallery6 from "../../images/blog/gallery6.png";
function Ourgallery() {
  return (
    <div className='my-5'>
      <p>OUR GALLERY</p>
      <img src={gallery1} className='m-1' alt='' />
      <img src={gallery2} className='m-1' alt='' />
      <img src={gallery3} className='m-1' alt='' />
      <img src={gallery4} className='m-1' alt='' />
      <img src={gallery5} className='m-1' alt='' />
      <img src={gallery6} className='m-1' alt='' />
    </div>
  );
}

export default Ourgallery;
