import React from "react";

export default function Heading({heading, para, headingcolor, paracolor}) {
  return (
    <div>
      <div className='col-lg-8 heading mx-auto py-md-5  '>
        <h1 className={`text-center ${headingcolor}`}>{heading}</h1>
        <p className={`text-center h5 ${paracolor}`}>{para}</p>
      </div>
    </div>
  );
}
